$card-color: #f0f0f0;
$Text-color: hsl(229, 25%, 31%);
$border: hsl(224, 16%, 73%);
$light-steel-blue: #d7e4f5;
$gradient1: linear-gradient(hsl(230, 89%, 62%), hsl(230, 89%, 65%));
@mixin element{
    background-color: $light-steel-blue;
    border-radius: 50%;
    width: 140px;
    height: 140px;
    justify-content: center;
    align-items: center;
}
.scissors{
 
    @include element;
    border : 20px solid hsl(39, 89%, 53%) ;
    box-shadow: 0 12px 0  hsl(40, 84%, 40%);
    justify-content: center;
    align-items: center;
    pointer-events: auto;
    img {
        margin: 40px 40px;
        width: 60px !important;
      }
      &:hover{
        cursor: pointer;
        transform: scale(1.1);
      }
}

@media  screen and (max-width :768px) {
  .scissors {
   width: 70px;
   height: 70px;
   border : 8px solid hsl(39, 89%, 53%) ;
   box-shadow: 0 4px 0  hsl(40, 84%, 40%);
   justify-content: center;
   align-items: center;
       img {
           margin: 20px 20px;
           width: 30px !important;
         }
         &:hover{
           cursor: pointer;
           transform: scale(1.1);
         }
       }
 
}