$card-color: #f0f0f0;
$Text-color: hsl(229, 25%, 31%);
$border: hsl(224, 16%, 73%);

body {
  background: radial-gradient(circle, 
    hsl(214, 47%, 23%) 0%, 
    hsl(224, 48%, 20%) 50%, 
    hsl(237, 49%, 15%) 100%);
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}
.game-content{
  .rules{
    margin-left: 13px;
    margin-top: -80px;
    z-index: 45;
    position: relative;
  }
.rules-toggle-button{
  position: absolute;
  bottom: 80px;
  right: 80px;
  z-index: 1000;
  button{
    width: 130px;
    border-radius: 8px;
    outline: none;
    height: 40px;
    text-transform: uppercase;
    font-size: 16px;
    background: linear-gradient(90deg,  hsl(237, 49%, 23%), hsl(237, 49%, 20%));
    border: 2px solid $border;
    color:#f0f0f0;
    &:hover{
      background: linear-gradient(90deg,  hsl(237, 49%, 33%), hsl(237, 49%, 30%));
      cursor: pointer;
    }
  }
}
}
@media screen and (max-width: 768px) {  
  body{
    overflow-x: hidden;
  
   .game-container {
     .game {
        display: flex;
        flex-direction: column;
       .chosen-component {
          margin: auto 10px !important;
          margin-top: 50px !important;
        }
     
      }
    }
    .rules-toggle-button{
      position: absolute;
      left: 30%;
      z-index: 1000;
      top: 70%;
      button{
        width: 130px;
        border-radius: 8px;
        outline: none;
        height: 40px;
        text-transform: uppercase;
        font-size: 16px;
        background: linear-gradient(90deg,  hsl(237, 49%, 23%), hsl(237, 49%, 20%));
        border: 2px solid $border;
        color:#f0f0f0;
        &:hover{
          background: linear-gradient(90deg,  hsl(237, 49%, 33%), hsl(237, 49%, 30%));
          cursor: pointer;
        }
      }
    }
  }
  
}