$card-color: #f0f0f0;
$Text-color: hsl(229, 25%, 31%);
$border: hsl(224, 16%, 73%);
$light-steel-blue: #d7e4f5;

$light-salmon: #ffa07a;
$coral: #ff7f50;
$tomato: #ff6347;
$orange-red: #ff4500;
$dark-orange: #ff8c00;
$navy: #000080;
$dark-blue: #00008b;
$medium-blue: #0000cd;
$blue: #0000ff;
$royal-blue: #4169e1;
$cornflower-blue: #6495ed;
$sky-blue: #87ceeb;
$slate-blue: #6a5acd;
$medium-slate-blue: #7b68ee;
$dark-slate-blue: #483d8b;
$light-slate-blue: #8470ff;
$steel-blue: #4682b4;
$deep-sky-blue: #00bfff;
$powder-blue: #b0e0e6;
$light-blue: #add8e6;
$gradient1: linear-gradient(hsl(230, 89%, 62%), hsl(230, 89%, 65%));
$Barlow-font: "Barlow Semi Condensed", sans-serif;
@mixin element {
  background-color: $light-steel-blue;
  border-radius: 50%;
  width: 140px;
  height: 140px;
  justify-content: center;
  align-items: center;
}
.computer-choice {
    margin: auto 360px !important;
    margin-top: -145px !important;
    display: flex;
    flex-direction: row;
    gap: 30px;
    .the-house{
        display: flex;
        flex-direction: column;
        gap: 50px;
      
    }
    .results{
        button{
            display: block;
            width: 150px;
            height: 40px;
            border: none;
            border-radius: 5px;
            color: $Text-color;
            text-transform: uppercase;
            font-family: $Barlow-font;
            font-size: 16px;
            font-weight: 600;
        }
        .result{
            align-items: center;
            justify-content: center;
            margin-left: -100px;
            margin-top: 150px;
            width: 150px;
            h1{
                letter-spacing: 2px !important;
                font-size: 34px;
                font-weight: 700;
                color: #f0f0f0;
                font-family: $Barlow-font;
                text-transform: uppercase;
            }
        }
    }
 
  button {
    display: none;
  }
  .computer-side {
    h1 {
      letter-spacing: 2px !important;
      font-size: 24px;
      font-weight: 600;
      width: 400px;
      color: #f0f0f0;
      font-family: $Barlow-font;
      text-transform: uppercase;
    }
  }
}
.game-container {
  .game {
    display: flex;
    flex-direction: column;
    .chosen-component {
      margin: auto 400px !important;
      margin-top: 50px !important;
    }
    .triangle {
      position: fixed;
      z-index: 1;
      margin: 0 auto !important;
      align-items: center;
      justify-content: center;
      left: 530px;
      top: 370px !important;
    }
    .pick-side {
      margin: auto 400px !important;
      margin-top: 50px !important;
      h1 {
        letter-spacing: 2px !important;
        font-size: 24px;
        font-weight: 600;
        color: #f0f0f0;
        font-family: $Barlow-font;
        text-transform: uppercase;
      }
    }
    .choices-row-one {
      display: flex;
      flex-direction: row;
      align-items: center;
      position: relative;
      z-index: 2;
      width: 500px;
      gap: 80px;
      margin: 0 auto !important;
      justify-content: center;
      align-items: center;
      margin-top: -360px !important;
      right: 90px;
    }
    .choice-row-two {
      display: flex;
      flex-direction: row;
      align-items: center;
      width: 500px;
      gap: 50px;
      margin: 0 auto !important;
      justify-content: center;
      align-items: center;
      margin-top: 40px !important;
      right: 90px;
      position: relative;
      z-index: 2;
    }
  }
}
@media screen and (max-width: 768px) {
  .game-container {
    .game {
      .chosen-component {
        margin: auto 60px !important;
        margin-top: 50px !important;
      }
      .triangle {
        position: fixed;
        z-index: 1;
        margin: 0 auto !important;
        align-items: center;
        justify-content: center;
        left: 40px;
        top: 300px !important;
        .tri-image{
          width: 250px;
          margin: 0 auto !important;
          top: 350px !important;
          
        }
       
      }
      .pick-side {
        margin: auto 30px !important;
        margin-top: 50px !important;
        h1 {
          letter-spacing: 2px !important;
          font-size: 13px;
          font-weight: 600;
          color: #f0f0f0;
          font-family: $Barlow-font;
          text-transform: uppercase;
        }
      }
      .choices-row-one {
        display: flex;
        flex-direction: row;
        align-items: center;
        position: relative;
        z-index: 2;
        width: 500px;
        gap: 80px;
        margin: 0 auto !important;
        justify-content: center;
        align-items: center;
        margin-top: -250px !important;
        right: 90px;
      }
      .choice-row-two {
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 500px;
        gap: 50px;
        margin: 0 auto !important;
        justify-content: center;
        align-items: center;
        margin-top: 70px !important;
        right: 130px;
        position: relative;
        z-index: 2;
      }
    }
  }
  .computer-choice {
    margin: auto 180px !important;
    margin-top: -100px !important;
    display: flex;
    flex-direction: row;
    gap: 30px;
    .the-house{
        display: flex;
        flex-direction: column;
        gap: 20px;
        .comp-choice{
          margin-left: -180px !important;
        }
    
      
    }
    .results{
        button{
            display: block;
            width: 80px;
            height: 40px;
            border: none;
            border-radius: 5px;
            color: $Text-color;
            text-transform: uppercase;
            font-family: $Barlow-font;
            font-size: 14px;
            font-weight: 600;
        }
        .result{
            align-items: center;
            justify-content: center;
            margin-left: -240px;
            margin-top: 120px;
            width: 150px;
            h1{
                letter-spacing: 2px !important;
                font-size: 13px;
                font-weight: 700;
                color: #f0f0f0;
                font-family: $Barlow-font;
                text-transform: uppercase;
            }
        }
    }
    button {
      display: none;
    }
    .computer-side {
      margin-left: -160px;
      margin-top: 30px;
      h1 {
        letter-spacing: 2px !important;
        font-size: 13px;
        font-weight: 600;
        width: 70px;
        color: #f0f0f0;
        font-family: $Barlow-font;
        text-transform: uppercase;
      }
    }
  }
}