@import url("https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
$card-color: #f0f0f0;
$Text-color: hsl(229, 25%, 31%);
$border: hsl(224, 16%, 73%);
$light-steel-blue: #d7e4f5;

$Barlow-font: "Barlow Semi Condensed", sans-serif;

.Rules-container {
  padding: 20px;
  margin-left: 35%;
  margin-top: 10%;
  font-size: $Barlow-font;
  .rules {
    display: flex;
    width: 350px;
    height: 370px;
    background-color: $card-color;
    color: $Text-color;
    border-radius: 8px;
    padding: 10px;
    .top{
        display: flex !important;
        flex-direction: row;
        .close-button{
            position: relative;
            height: 50px;
            top: 40px;
            left:200px;
            img{
                width: 30px;
                height: 30px;
            }
        }
    }
    h1 {
      font-family: $Barlow-font;
      font-size: 30px;
      font-weight: 700;
      color: $Text-color;
      text-transform: uppercase;
      padding: 10px;
    }
    .rules-content {
      display: flex;
      flex-direction: column;
      gap: 30px;
      width: 200px;
      height: 300px;
      margin-top: 90px;
      margin-left: -50px;
      .row1 {
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 500px;
        margin-left: -60px;
        gap: 50px;
        .rule1 {
          display: flex;
          flex-direction: row;
          align-items: center;
          .arrow-left {
            width: 20px;
            height: 20px;
            position: relative;
            right: 10px;
            .beats-text {
              font-family: $Barlow-font;
              font-size: 16px;
              font-weight: 700;
              text-transform: uppercase;
              color: $border;
              position: absolute;
              top: -30px;
              text-align: center;
              left: 30px;
            }
            img {
              width: 100px;
              height: 50px;
            }
          }
        }
        .rule2 {
          display: flex;
          flex-direction: row;
          .right-side-arrow {
            transform: rotate(-240deg);
            position: relative;
            top: 60px;
            right: 170px;
            .beat-text {
              font-family: $Barlow-font;
              font-size: 16px;
              font-weight: 700;
              text-transform: uppercase;
              color: $border;
              position: absolute;
              top: -40px;
              text-align: center;
              left: 30px;
              transform: rotate(240deg);
            }

            img {
              width: 80px;
              height: 50px;
            }
          }
        }

        .paper-rule {
          background-color: $light-steel-blue;
          border-radius: 50%;
          border: 15px solid $border;
          width: 90px;
          height: 90px;
          justify-content: center;
          align-items: center;

          img {
            justify-content: center;
            align-items: center;
            padding: 20px;
          }
        }
        .scissors-rule {
          background-color: $light-steel-blue;
          border-radius: 50%;
          border: 15px solid $border;
          width: 90px;
          height: 90px;
          justify-content: center;
          align-items: center;
          img {
            justify-content: center;
            align-items: center;
            padding: 20px;
          }
        }
      }
      .row2 {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        .left-side-arrow {
          transform: rotate(45deg);
          position: relative;
          bottom: 70px;
          right: 30px;
          .beat2-text {
            font-family: $Barlow-font;
            font-size: 16px;
            font-weight: 700;
            text-transform: uppercase;
            color: $border;
            position: absolute;
            top: 40px;
            text-align: center;
            right: 20px;
            transform: rotate(-45deg);
          }
          img {
            width: 80px;
            height: 50px;
          }
        }

        .rock-rule{
          background-color: $light-steel-blue;
          border-radius: 50%;
          border: 15px solid $border;
          width: 90px;
          height: 90px;
          justify-content: center;
          align-items: center;
          margin-left: -70px;
          img {
            justify-content: center;
            align-items: center;
            padding: 20px;
          }
        }
      }
    }
  }
}
