$card-color: #f0f0f0;
$Text-color: hsl(229, 25%, 31%);
$border: hsl(224, 16%, 73%);
$Barlow-font: "Barlow Semi Condensed", sans-serif;

@mixin game-elements {
  width: 80px !important;
  height: 30px;
  text-transform: uppercase;
  font-size: 20px !important;
}

.header {
  width: 680px !important;
  height: 100px !important;
  border: 3px solid hsl(217, 16%, 45%) !important;
  margin: 0 auto !important;
  padding: 20px !important;
  margin-top: 80px !important;
  border-radius: 12px !important;
  display: flex;
  flex-direction: row;
  gap: 370px !important;
  font-family: $Barlow-font;
  color: #fff;
  .intro {
    display: flex !important;
    flex-direction: column !important;
    height: 100px !important;
    font-family: $Barlow-font;
    color: #fff;
    gap: 2px !important;
    margin-top: -35px;
    .rock-heading {
      @include game-elements;
    }
    .paper-heading {
      @include game-elements;
    }
    .scissors-heading {
      @include game-elements;
    }
  }
  .score {
    background-color: #fff;
    width: 200px !important;
    height: 100px !important;
    border-radius: 7px;
    padding: 5px;
    margin-top: -5px;
    h1 {
      color: $Text-color;
      font-family: $Barlow-font;
      font-size: 60px;
      letter-spacing: 3px !important;
      font-weight: 700;
      margin: 0;
      text-transform: uppercase;
      text-align: center;
    }
    p {
      color: hsl(229, 64%, 46%);
      text-align: center;
      font-family: $Barlow-font;
      font-size: 18px;
      letter-spacing: 3px !important;
      font-weight: 700;
      margin: 0;
      text-transform: uppercase;
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    width: 250px !important;
    height: 80px !important;
    border: 3px solid hsl(217, 16%, 45%) !important;
    margin-left: 4px !important;
    padding: 20px !important;
    margin-top: 80px !important;
    border-radius: 12px !important;
    display: flex;
    flex-direction: row;
    gap: 300px !important;
    font-family: $Barlow-font;
    color: #fff;
    .intro {
      display: flex !important;
      flex-direction: column !important;
      height: 100px !important;
      font-family: $Barlow-font;
      color: #fff;
      gap: 2px !important;
      margin-top: -35px;
      .rock-heading {
        width: 80px !important;
        text-transform: uppercase;
        font-size: 12px !important;
      }
      .paper-heading {
        width: 80px !important;
        height: 30px;
        text-transform: uppercase;
        font-size: 12px !important;
      }
      .scissors-heading {
        width: 80px !important;
        height: 30px;
        text-transform: uppercase;
        font-size: 12px !important;
      }
    }
    .score {
      background-color: #fff;
      height: 90px !important;
      border-radius: 7px;
      width: 70px;
      padding: 5px;
      margin-top: -11px;
      margin-left: -210px;
      h1 {
        color: $Text-color;
        font-family: $Barlow-font;
        font-size: 60px;
        letter-spacing: 3px !important;
        font-weight: 700;
        margin: 0;
        text-transform: uppercase;
        text-align: center;
      }
      p {
        color: hsl(229, 64%, 46%);
        text-align: center;
        font-family: $Barlow-font;
        font-size: 18px;
        letter-spacing: 3px !important;
        font-weight: 700;
        margin: 0;
        text-transform: uppercase;
      }
    }
  }
}
